.player-body {
    width: 100%;
    height: 40%;
    margin: 3% 0%;
}

.player-left {
    width: 34%;
}

.player-right {
    width: 63%;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.song-title {
    align-items: center;
    font-size: 58px;
    text-transform: uppercase;
    font-weight: bold;
    margin: 0;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
    text-align: center;
}

.song-artist {
    font-size: 14px;
    font-weight: 500;
}

.player-right-bottom {
    align-items: center;
    width: 100%;
    flex-direction: column;
}

.song-duration {
    width: 50%;
    justify-content: space-between;
    margin-bottom: 20px;
    align-items: center;
}

.duration {
    font-size: 20px;
    font-weight: 700;
    margin: 0;
}